'use client'

import { useState } from 'react'

type OptionalDoc = { title: string; file: File | null }

type Props = {
  requiredDocs: string[]
  requiredUploads: Record<string, File | null>
  onRequiredChange: (docTitle: string, file: File | null) => void

  optionalDocs: OptionalDoc[]
  onOptionalChange: (index: number, key: 'title' | 'file', value: any) => void
  onAddOptional: () => void
  onRemoveOptional: (index: number) => void

  errors?: {
    required?: Record<string, string>
    optional?: Record<number, string>
  }
}

const MAX_FILE_SIZE_MB = 2
const MAX_FILE_SIZE_BYTES = MAX_FILE_SIZE_MB * 1024 * 1024

const validateFile = (file: File | undefined | null): string | null => {
  if (!file) return null
  if (file.type !== 'application/pdf') return 'Only PDF files are allowed.'
  if (file.size > MAX_FILE_SIZE_BYTES) return `File is too large. Max size is ${MAX_FILE_SIZE_MB}MB.`
  return null
}

export default function FileUpload({
  requiredDocs,
  requiredUploads,
  onRequiredChange,
  optionalDocs,
  onOptionalChange,
  onAddOptional,
  onRemoveOptional,
  errors = {},
}: Props) {
  const [localErrors, setLocalErrors] = useState<{
    required: Record<string, string>
    optional: Record<number, string>
  }>({ required: {}, optional: {} })

  const handleRequiredChange = (title: string, file: File | null) => {
    const error = validateFile(file)
    setLocalErrors((prev) => ({
      ...prev,
      required: { ...prev.required, [title]: error || '' },
    }))
    if (!error) onRequiredChange(title, file)
  }

  const handleOptionalFileChange = (index: number, file: File | null) => {
    const error = validateFile(file)
    setLocalErrors((prev) => ({
      ...prev,
      optional: { ...prev.optional, [index]: error || '' },
    }))
    if (!error) onOptionalChange(index, 'file', file)
  }

  return (
    <div className="space-y-10">
      <section>
        <h3 className="text-lg font-semibold mb-3 text-gray-800">Mandatory Documents</h3>
        <div className="space-y-5">
          {requiredDocs.map((title) => (
            <div key={title}>
              <label className="block text-sm font-medium text-gray-700 mb-1">{title}</label>
              <input
                type="file"
                required
                accept="application/pdf"
                onChange={(e) => handleRequiredChange(title, e.target.files?.[0] || null)}
                className="block w-full text-sm border border-gray-300 rounded-lg file:mr-4 file:py-1.5 file:px-4 file:border-0 file:bg-blue-50 file:text-blue-700 hover:file:bg-blue-100"
              />
              {requiredUploads[title] && !localErrors.required[title] && (
                <p className="text-xs text-green-600 mt-1">✅ {requiredUploads[title]?.name}</p>
              )}
              {localErrors.required[title] && (
                <p className="text-xs text-red-600 mt-1">❌ {localErrors.required[title]}</p>
              )}
              {errors.required?.[title] && !localErrors.required[title] && (
                <p className="text-xs text-red-600 mt-1">❌ {errors.required[title]}</p>
              )}
            </div>
          ))}
        </div>
      </section>

      <section>
        <h3 className="text-lg font-semibold mb-3 text-gray-800">Optional Documents</h3>
        <div className="space-y-6">
          {optionalDocs.map((doc, index) => (
            <div key={index} className="grid grid-cols-1 md:grid-cols-3 gap-4 items-center">
              <input
                type="text"
                placeholder="Document Title"
                value={doc.title}
                className="w-full border border-gray-300 rounded-md p-2"
                onChange={(e) => onOptionalChange(index, 'title', e.target.value)}
              />

              <div>
                <input
                  type="file"
                  accept="application/pdf"
                  onChange={(e) => handleOptionalFileChange(index, e.target.files?.[0] || null)}
                  className="block w-full text-sm border border-gray-300 rounded-lg file:mr-4 file:py-1.5 file:px-4 file:border-0 file:bg-gray-50 file:text-gray-700 hover:file:bg-gray-100"
                />
                {doc.file && !localErrors.optional[index] && (
                  <p className="text-xs text-green-600 mt-1">✅ {doc.file.name}</p>
                )}
              </div>

              <button
                type="button"
                onClick={() => onRemoveOptional(index)}
                className="text-red-500 text-sm hover:underline"
              >
                Remove
              </button>

              {(localErrors.optional[index] || errors.optional?.[index]) && (
                <p className="col-span-3 text-sm text-red-600">
                  ❌ {localErrors.optional[index] || errors.optional?.[index]}
                </p>
              )}
            </div>
          ))}

          <button
            type="button"
            onClick={onAddOptional}
            className="mt-3 text-blue-600 text-sm hover:underline"
          >
            + Add Another Optional Document
          </button>
        </div>
      </section>
    </div>
  )
}
