'use client'

import React from 'react'

interface PageWrapperProps {
  title?: string
  children: React.ReactNode
  className?: string
  contentClassName?: string
}

export default function PageWrapper({
  title,
  children,
  className = '',
  contentClassName = '',
}: PageWrapperProps) {
  return (
    <main className={`min-h-screen bg-[#f0f2f5] ${className}`}>
      <header className="bg-gradient-to-r from-[#0057A0] to-[#C8102E] text-white py-6 text-center">
        <h1 className="text-2xl font-bold">Prime Pillars – Data Entry V2</h1>
      </header>
      <div className="max-w-4xl mx-auto mt-10 bg-white p-8 rounded-md shadow-md">
        {title && (
          <h2 className="text-xl font-semibold text-[#0057A0] border-b-2 border-[#0057A0] pb-1 mb-6">{title}</h2>
        )}
        <div className={`space-y-6 ${contentClassName}`}>{children}</div>
      </div>
    </main>
  )
}
