'use client'

import React from 'react'

interface FormSelectProps extends React.SelectHTMLAttributes<HTMLSelectElement> {
  label: string
  name: string
  error?: string
  children: React.ReactNode
  className?: string
}

export default function FormSelect({
  label,
  name,
  error,
  children,
  className = '',
  disabled,
  ...props
}: FormSelectProps) {
  return (
    <div className="flex flex-col space-y-1">
      <label
        htmlFor={name}
        className="block font-semibold text-gray-800"
      >
        {label}
      </label>

      <select
        id={name}
        name={name}
        aria-invalid={!!error}
        disabled={disabled}
        className={`w-full p-3 rounded-md border text-sm transition focus:outline-none ${
          error ? 'border-red-500 focus:ring-2 focus:ring-red-300' : 'border-gray-300 focus:ring-2 focus:ring-[#0057A0]'
        } ${disabled ? 'bg-gray-100 cursor-not-allowed text-gray-500' : 'bg-white'} ${className}`}
        {...props}
      >
        {children}
      </select>

      {error && (
        <p className="text-sm text-red-600">{error}</p>
      )}
    </div>
  )
}
